body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    line-height: 60px;
    background-color: #333;
    display: block;
}
.App-footer{
  background-color: #dcd9d8;
  min-height: 55vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}
#bigText{
  text-align: center;
  padding-top: 40px;
}
#smallText{
  text-align: center;
  font-size: 15px;
  margin-left: 550px; 
  margin-right: 550px;
  margin-top: 25px;
}
#smallText_card{
  text-align: center;
  font-size: 15px;
  margin-top: 25px;
}
.App-footer1{
  background-color: white;
  min-height: 120vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 250px;
  margin-top: 100px;
  font-size: calc(10px + 2vmin);
  color: black;

}

.card {
	margin-top: 40px;
	margin-left: 80px;
    width: 60%;
    height: 100%;
    background-color: #dcd9d8;
    border: none;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    background-color: white;
}
.navbar{
	background-color: transparent;
	position: fixed;
	height: 100px;
	padding-left: 300px;
}

.navbar-brand {
	height:90px;
	color: transparent;
  background-image: url(/test_website_react/static/media/logo_peep.a2770f1f.jpg);
}
.nav-item{
	padding-left: 5px;
	padding-right: -10px;

}

a {
  color: #acacac;
  font-weight: 400;
  font-size: 1.4em;
}

.jumbotron {
  background-image: url(/test_website_react/static/media/homebg.d1aae9b8.png);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  color: black;
}

.home_btns{
    
    border-radius: 100%;
    width: 500px;
}
.home_btns a{
    
}

.myButton_red {
    background-color:#FF0000;
    border-radius:28px;
    border:1px solid #18ab29;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-family:Arial;
    font-size:17px;
    padding:12px 40px;
    text-decoration:none;
    text-shadow:0px 1px 0px #780727;
}

.myButton_white{
    background-color:#F8F8F8;
    border-radius:28px;
    border:1px solid #18ab29;
    display:inline-block;
    cursor:pointer;
    color:#0C0B0B;
    font-family:Arial;
    font-size:17px;
    padding:10px 28px;
    text-decoration:none;
    text-shadow:0px 1px 0px #780727;
    margin-left: 15px;
}

.myButton_green {
    background-color:#28a007;
    border-radius:28px;
    border:1px solid #18ab29;
    display:inline-block;
    cursor:pointer;
    color:#FFFFFF;
    font-family:Arial;
    font-size:17px;
    padding:10px 28px;
    text-decoration:none;
    text-shadow:0px 1px 0px #780727;
}

.divider{
    width:75px;
}
